.page {
  position: relative;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  min-height: 100vh;
  min-width: $width-mobile;

  @include mobile-md {
    overflow-x: hidden;
  }
}

.page__content {
  flex: 1 0 auto;

  padding-top: var(--header-height);
}

.page__footer {
  flex-shrink: 0;
}
